function createMixin(_super){
	return {
		refresh: function() {
			this._linkToTaskStore();
			_super.prototype.refresh.apply(this, arguments);
		},

		_linkToTaskStore: function () {

			if (this.$config.rowStore && this.$gantt.$data.tasksStore) {
				var tasks = this.$gantt.$data.tasksStore;
				var ownStore = this.$config.rowStore;

				if (!tasks["_attached_" + ownStore.name]) {
					tasks["_attached_" + ownStore.name] = true;
					var updateDelay;
					tasks.attachEvent("onStoreUpdated", function (id, item, mode) {
						if(window.requestAnimationFrame){
							cancelAnimationFrame(updateDelay);
							updateDelay = requestAnimationFrame(function(){
								ownStore.refresh();
							});
						}else{
							ownStore.refresh();
						}

					});
				}

				this._linkToTaskStore = function () {
				};
			}
		}
	};
}

module.exports = createMixin;